.about-page{
  @media screen and (min-width: 993px){
    padding: 10em 0;
    .about-text{
      p{
        font-size: 1.125em;
      }
    }
    .about__inner{
      grid-template-columns: 37.5625em 36.625em;
    }
    .about__col:nth-child(2){
      padding-top: 1.5em;
    }
  }
  .about__inner{
    max-width: 100%;
  }
}

.section{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 3.25em;
  }
}

.team{
  padding: 12.5em 0 6.25em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .3125em));
    gap: .625em;
  }
}

.team-card{
  display: grid;
  grid-template-columns: calc(100% - 17.1875em) 17.1875em;
  background-color: $grayBg;
  align-items: flex-end;
  &__img{
    background-color: $dark;
    @include imgCover(100%);
    &:hover{
      background-color: transparent;
      img{
        mix-blend-mode: normal;
      }
    }
    img{
      mix-blend-mode: luminosity;
    }
  }
  &__content{
    padding: 2.25em;
  }
  &__info{
    max-width: 14em;
    @include grid100gap(.75em);
  }
  .text24{
    line-height: 117%;
  }
}

.certificates2{
  padding: 6.25em 0 12.5em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(5,20%);
  }
}

.certificate-card2{
  padding: 1.5em 2.5em;
  background-color: $grayBg;
  transition: all .3s;
  position: relative;
  &:before,
  &:after{
    opacity: 0;
    pointer-events: none;
  }
  &:before{
    background-color: $dark;
    z-index: 3;
    transition: inherit;
  }
  &:after{
    content: '';
    display: block;
    position: absolute;
    @include centerAbsolute;
    width: 2.5em;
    height: 2.5em;
    background-image: url(../img/plus.svg);
    @include bgImgSize(100%);
    z-index: 4;
  }
  &:hover{
    background-color: $dark;
    &:after{
      opacity: 1;
    }
    &:before{
      opacity: .7;
    }
  }
  &._green{
    background-color: #E8F8F3;
  }
  &__img{
    display: block;
    @include imgCover(132%);
  }
}

.history{
  padding: 11em 0 17em;
  background-color: $dark;
  color: #fff;
  overflow: hidden;
  &__tabs{
    display: flex;
    flex-wrap: wrap;
    margin-top: 1em;
  }
  &__tab{
    margin: 1em 1.88em 0 0;
    opacity: 0.5;
    transition: all .3s;
    cursor: pointer;
    &:hover,
    &._active{
      opacity: 1;
    }
    &._active{
      pointer-events: none;
    }
  }
  &__desc{
    margin-top: 2em;
    max-width: 87.5625rem;
    .text14{
      line-height: 207%;
    }
  }
}

.history-slider{
  margin-top: 5em;
  .swiper{
    &-slide{
      width: 60em;
      padding-right: 11.5em;
      &:not(.swiper-slide-active){
        .history-card{
          &__img{
            background-color: $dark;
            img{
              mix-blend-mode: luminosity;
            }
          }
        }
      }
    }
    &-scrollbar{
      background-color: rgba(#fff,.1);
      &-drag{
        background-color: #fff;
      }
    }
  }
}

.history-card{
  &__img{
    margin-bottom: 5em;
    @include imgCover(61.7%);
  }
  &__content{
    @include grid100gap(1.5em);
  }
  .text14{
    line-height: 207%;
  }
}

.team2{
  padding: 6.25em 0 12.5em;
  &__img{
    margin-top: 4em;
    background-color: $dark;
    &:hover{
      background-color: transparent;
      img{
        mix-blend-mode: normal;
      }
    }
    img{
      width: 100%;
      display: block;
      mix-blend-mode: luminosity;
    }
  }
}

.certificates-page{
  padding: 12.5em 0;
}

.docs{
  padding: 12.5em 0;
  &__items{
    margin-top: 1em;
  }
}

.doc-card{
  border-bottom: 1px solid rgba($black,.2);
  &._active{
    .doc-card__show{
      opacity: 1;
    }
    .doc-card__btn{
      svg{
        transform: rotate(90deg);
      }
    }
  }
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 6.25em;
    padding: .5em 0;
    opacity: .65;
    transition: all .4s;
    &:hover{
      opacity: 1;
      cursor: pointer;
    }
  }
  &__btn{
    width: 2.5em;
    flex-shrink: 0;
    margin-left: 1em;
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__hidden{
    padding-bottom: 3.4em;
    display: none;
    .certificates2__grid{
      margin: 0;
    }
  }
}

.requisite{
  padding: 12.5em 0;
  &__items{
    margin-top: 4em;
  }
  &__nav{
    display: flex;
    margin-top: 4.38em;
  }
}

.requisite-item{
  min-height: 6.25em;
  padding: .5em 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid rgba($black,.2);
  &:first-child{
    border-top: 1px solid rgba($black,.2);
  }
  &__title{
    opacity: .65;
    margin-right: 1em;
  }
  &__desc{
    text-align: right;
  }
}


@media screen and (max-width: 992px){
  .about-page{
    .text36{
      display: none;
    }
    .about-text{
      margin-top: 0;
    }
  }

  .section{
    .container{
      position: relative;
      padding-bottom: 4em;
    }
    &__top{
      margin-bottom: 1em;
    }
    &__nav{
      position: absolute;
      bottom: 0;
      left: 0;
      display: flex;
      width: 100%;
      padding: 0 .625em;
    }
  }

  .team{
    padding: 2.5em 0 1.25em;
    &__grid{
      @media screen and (min-width: 577px){
        grid-template-columns: repeat(4,calc(25% - 1.875em / 4));
      }
    }
  }

  .team-card{
    grid-template-columns: 100%;
    &__media{
      order: -1;
    }
    &__content{
      padding: 1em;
    }
  }

  .certificates2{
    padding: 1.25em 0 2.5em;
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3));
    }
  }

  .history{
    padding: 2.5em 0;
    &__tabs{
      margin: 0;
    }
  }

  .history-slider{
    margin-top: 1.5em;
    .swiper{
      &-slide{
        width: 24em;
        padding-right: 1.5em;
      }
    }
  }

  .history-card{
    &__img{
      margin-bottom: 1em;
    }
    &__content{
      gap: .75em;
    }
    .text14{
      line-height: 150%;
    }
  }

  .certificates-page,
  .team-page{
    .container{
      padding-bottom: 0;
    }
  }

  .docs,
  .certificates-page{
    padding: 2.5em 0;
  }

  .doc-card{
    &__show{
      min-height: 3.125em;
    }
    &__title{
      font-size: .875em;
    }
    &__btn{
      width: 1.875em;
    }
  }

  .requisite{
    padding: 2.5em 0;
    &__nav,
    &__items{
      margin-top: 1.5em;
    }
  }

  .requisite-item{
    min-height: 3.125em;
  }
}


@media screen and (max-width: 576px){
  .section{
    &__link{
      width: 100%;
    }
  }

  .team-card{
    &__content{
      padding: .5em;
    }
    &__info{
      gap: .5em;
    }
    .text24{
      font-size: .875em;
    }
    .text14{
      font-size: .625em;
    }
  }

  .certificates2{
    &__grid{
      grid-template-columns: repeat(2,50%);
    }
  }

  .certificate-card2{
    padding: 1em;
  }

  .history{
    &__tabs{
      display: none;
    }
  }

  .history-slider{
    .swiper{
      &-slide{
        width: 95%;
        padding-right: 1em;
      }
    }
  }

  .requisite-item{
    @include grid100gap(.75em);
    &__title{
      .text18{
        &:after{
          content: ':';
        }
      }
    }
    &__desc{
      text-align: left;
    }
  }
}
