.service{
  position: relative;
  background-color: $dark;
  color: #fff;
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    mix-blend-mode: luminosity;
  }
  &__inner{
    position: relative;
    z-index: 2;
    padding: 12.3em 0;
    min-height: 50.6em;
    display: flex;
    align-items: flex-end;
  }
  &__content{
    max-width: 40em;
    width: 100%;
    @include grid100gap(2em);
  }
  &__nav{
    display: flex;
    margin-top: 1em;
  }
  &__items{
    display: flex;
    align-items: center;
    margin-top: -.5em;
  }
  &__item{
    margin-right: 1.88em;
  }
}

.clients-service{
  background-color: $grayBg;
}

.about-service{
  background-color: transparent;
  @media screen and (min-width: 993px){
    .about__inner{
      align-items: center;
    }
  }
}

.prices{
  padding: 6.25em 0 12.5em;
}

.advantages-service{
  padding-bottom: 6.25em;
}




@media screen and (max-width: 992px){
  .service{
    &__inner{
      min-height: unset;
      padding: 2.5em 0;
    }
    &__content{
      gap: 1em;
    }
  }

  .prices{
    padding: 1.25em 0 2.5em;
  }

  .advantages-service{
    padding-bottom: 1.25em;
  }
}


@media screen and (max-width: 992px){
  .service{
    &__items{
      display: block;
    }
    &__item{
      margin: .5em 0 0;
    }
  }
}
