@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.swiper{
  &-scrollbar,
  &-scrollbar-drag{
    &:before{
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      height: 4em;
      width: 100%;
      transform: translateY(-50%);
    }
  }
  &-scrollbar{
    margin-top: 4em;
    width: 100%;
    height: .0625em;
    border-radius: 0;
    background-color: rgba($black,.1);
    &-drag{
      background-color: $dark;
      &:before{
        z-index: 2;
      }
    }
  }
}

._cover{
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}


@media screen and (max-width: 992px){
  .swiper{
    &-scrollbar{
      display: none;
    }
  }
}
