.textpage{
  padding: 12.5em 0;
}

.text{
  margin-top: 2em;
  @include grid100gap(1.5em);
  p{
    line-height: 207%;
    letter-spacing: 0.0175rem;
  }
}

.error{
  padding: 17em 0 12em;
  flex-grow: 1;
  background-image: url(../img/404.webp);
  background-size: cover;
  background-position: center;
  &__info{
    @include grid100gap(1em);
    max-width: 35.5em;
  }
  &__text{
    margin-top: 2em;
    max-width: 40em;
  }
  &__nav{
    margin-top: 4em;
    display: flex;
  }
  &__btn{
    margin-right: 1.25em;
  }
}

.error-info{
  margin-top: 6em;
  max-width: 55em;
  display: flex;
  justify-content: space-between;
  &__col{
    max-width: 23.5em;
    position: relative;
    &:not(:last-child){
      padding-right: 2.75em;
      &:before{
        content: '';
        position: absolute;
        background: rgba(0, 0, 0, 0.10);
        width: 1px;
        height: 5.1875rem;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }
  &__desc{
    margin-top: 1.5em;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    white-space: nowrap;
    @include lastM(0 0 .25em);
  }
}

.site-map{
  display: flex;
  flex-wrap: wrap;
  &__col{
    margin: 4em 3em 0 0;
    min-width: 12.5em;
  }
  &__links{
    margin-top: 2em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    opacity: .8;
    @include lastM(0 0 2em);
    &:hover{
      opacity: 1;
    }
  }
}


@media screen and (max-width: 992px){
  .textpage{
    padding: 2.5em 0;
  }

  .text{
    margin-top: 1.5em;
  }

  .error{
    padding: 2.5em 0;
    &__info{
      gap: .5em;
      max-width: 22.5em;
    }
    &__text{
      margin-top: 1em;
      max-width: 27em;
    }
    &__nav{
      margin-top: 1em;
    }
  }

  .error-info{
    margin-top: 1.5em;
    @include grid100gap(1em);
    &__col{
      &:not(:last-child){
        padding: 0 0 1em;
        &:before{
          width: 100%;
          height: 1px;
          right: 0;
          bottom: 0;
          top: unset;
          transform: unset;
        }
      }
    }
    &__desc{
      margin-top: 1em;
    }
  }

  .site-map{
    &__col{
      min-width: 10em;
      margin: 2.5em 1.5em 0 0;
    }
    &__links{
      margin-top: 1.5em;
    }
    &__link{
      @include lastM(0 0 1em);
    }
  }
}
