.objects{
  padding: 12.5em 0;
  .container{
    max-width: 87.255em;
  }
  &__grid{
    margin-top: 4em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .3em));
    gap: .6em;
  }
}

.object-card{
  &:hover{
    color: #fff;
  }
  &__img{
    padding-bottom: 55.26%;
    z-index: 1;
  }
  &__content{
    position: absolute;
    z-index: 4;
    left: 0;
    bottom: 0;
    padding: 5em 2.25em;
    width: 100%;
  }
  &__info{
    max-width: 18em;
    .text24{
      letter-spacing: 0.03rem;
    }
  }
}

.pagination{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 6.25em;
  &__link{
    width: 2.5em;
    height: 2.5em;
    @include center;
    color: rgba($dark,.7);
    svg{
      width: 100%;
      height: auto;
    }
    span{
      font-size: 1.25rem;
      line-height: 120%;
      font-weight: 700;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
    &._active{
      color: $dark;
      pointer-events: none;
    }
  }
}

.object{
  @media screen and (min-width: 993px){
    .service__content{
      gap: 1.75em;
    }
  }
}

.gallery{
  padding: 12.5em 0;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .3125em));
    gap: .625em;
    margin-top: 4em;
  }
}

.gallery-card{
  &__img{
    display: block;
    @include imgCover(55.27%);
  }
}



@media screen and (max-width: 992px){
  .objects{
    padding: 2.5em 0;
    &__grid{
      margin-top: 1.5em;
    }
  }

  .object-card{
    &__content{
      padding: 1em;
    }
    &__info{
      max-width: 14em;
    }
  }

  .pagination{
    margin-top: 1.5em;
  }

  .gallery{
    padding: 2.5em 0;
    &__grid{
      margin-top: 1.5em;
    }
  }
}


@media screen and (max-width: 576px) {
  .objects,
  .gallery{
    &__grid {
      grid-template-columns: 100%;
    }
  }
}
