.header{
  background-color: $dark;
  color: #fff;
  position: sticky;
  top: 0;
  z-index: 10;
  @media screen and (min-width: 993px){
    .container{
      max-width: 1920px;
    }
  }
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 7.875em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__calc{
    padding: 0 .8em;
    height: 5em;
    display: flex;
    align-items: center;
    color: $primary;
    background-color: rgba($primary,.25);
    &:hover{
      background-color: rgba($primary,.35);
    }
    img{
      width: 1.375em;
      flex-shrink: 0;
      display: block;
      margin-left: .44em;
    }
  }
}

.menu{
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex-grow: 1;
  max-width: 67.375em;
}

.header-contacts{
  display: flex;
  align-items: center;
  margin-right: 1.5em;
  .social{
    margin-right: .94em;
  }
}

.social{
  display: flex;
  align-items: center;
  &__item{
    width: 1.5em;
    flex-shrink: 0;
    @include lastM(0 .87em 0 0);
    img{
      width: 100%;
      display: block;
      transition: all .3s;
    }
    &:hover{
      img{
        transform: translateY(-25%);
      }
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  margin-right: auto;
  &__link{
    @include lastM(0 2.25em 0 0);
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    padding: 1em 0;
    &__logo{
      position: relative;
      z-index: 11;
    }
    .social{
      justify-content: center;
      margin: 1em 0;
    }
    &__calc{
      height: 3.5em;
      padding: 0 2em;
      justify-content: space-between;
      margin-top: 1em;
    }
    &._open{
      .burger{
        background-image: url(../img/burger2.svg);
        background-size: 85% auto;
      }
      .menu{
        transform: translateX(0%);
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .burger{
    margin-left: auto;
    display: flex;
    flex-shrink: 0;
    width: 1.75em;
    height: 1.75em;
    position: relative;
    z-index: 11;
    transition: all .0s;
    background-image: url(../img/burger.svg);
    @include bgImgSize(100%);
  }

  .menu {
    position: fixed;
    bottom: 0;
    left: 0;
    overflow-y: auto;
    height: $fullHeight;
    width: 100%;
    background: $dark;
    z-index: 10;
    padding: 5em 1em 2em;
    margin: 0;
    flex-direction: column;
    justify-content: unset;
    opacity: 0;
    pointer-events: none;
    transform: translateX(-200%);
    transition: all .4s;
  }

  .nav,
  .header-contacts{
    flex-direction: column;
    align-items: center;
    &__link{
      @include lastM(0 0 1em);
    }
  }

  .nav{
    margin: 0;
  }

  .header-contacts{
    margin-left: auto;
    margin-right: auto;
  }

  .header-contacts{
    margin-top: 2em;
  }
}
