.main-preview{
  display: grid;
  grid-template-columns: repeat(5,20%);
  &__item{
    background: $dark;
    transition: all .3s;
    &._active,
    &:hover{
      cursor: pointer;
      background-color: transparent;
    }
  }
  &__img{
    @include imgCover(56.7%);
    img{
      mix-blend-mode: luminosity;
    }
  }
  &__item{
    &._big{
      grid-column: span 2;
      .main-preview__img{
        padding-bottom: 28.35%;
      }
    }
  }
}

.main-slider{
  overflow: hidden;
  color: #fff;
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
  }
}

.main-slide{
  padding: 13.6em 0 8.25em;
  background-size: cover;
  background-position: center;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $black;
    opacity: .25;
    z-index: 2;
  }
  &__content{
    max-width: 42.5em;
    position: relative;
    z-index: 3;
    @include grid100gap(2.56em);
  }
  &__nav{
    display: flex;
    margin-top: .5em;
  }
  &__btn{
    @include lastM(0 1.25em 0 0);
  }
}

.contacts-page{
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.contacts{
  min-height: 47.625em;
  position: relative;
}

.map{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.contact-info{
  flex-grow: 1;
  min-height: 100%;
  height: 100%;
  width: 37.5em;
  padding: 4.386em;
  position: relative;
  z-index: 3;
  margin-left: auto;
  background-color: rgba($primary,.9);
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  &__items{
    margin-top: 1.5em;
    @include grid100gap(2.1em);
  }
  &__nav{
    display: flex;
    margin-top: 3.12em;
  }
  &__btn{
    @include lastM(0 1.25em 0 0);
  }
}

.contact-item{
  @include grid100gap(1.5em);
  max-width: 23em;
  &__title{
    display: flex;
    align-items: center;
    &:after{
      content: '';
      flex-shrink: 0;
      display: block;
      width: 3.125rem;
      height: 0.125rem;
      background-color: #fff;
      margin-left: .62em;
    }
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    @include lastM(0 0 .75em);
    &:hover{
      color: $grayBg;
    }
  }
}

.clients{
  padding: 8.75em 0;
  &__inner{
    max-width: 76.25em;
    margin: 0 auto;
    width: 100%;
  }
  &__top{
    display: flex;
    justify-content: space-between;
    &-left{
      max-width: 31.25em;
    }
    &-right{
      max-width: 28.25em;
    }
  }
}

.clients-slider{
  margin-top: 7.2em;
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-wrapper{
      align-items: center;
    }
    &-slide{
      width: auto;
      padding-right: 4em;
    }
  }
  &__img{
    height: 10.3125em;
    display: flex;
    align-items: center;
    img{
      height: 100%;
      width: auto;
      display: block;
    }
  }
}

.advantages{
  padding: 6.25em 0 12.5em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(5,calc(20% - 3em / 5));
    gap: .75em;
    margin-top: 4.375em;
  }
}

.advantage-card{
  padding: 2.1875em 1.25em 2.5em;
  background-color: $grayBg;
  transition: all .3s;
  &:hover{
    background-color: $dark;
    color: #fff;
    .advantage-card__title:before{
      background-color: #fff;
    }
  }
  &__icon{
    height: 4em;
    margin-bottom: 3.63em;
    svg{
      height: 100%;
      width: auto;
    }
  }
  &__info{
    @include grid100gap(1.25em);
  }
  &__title{
    padding-bottom: 1.25em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 3.125rem;
      height: 0.125rem;
      background: $black;
      transition: all .3s;
    }
  }
}

.services{
  margin: 0 auto;
  width: 100%;
  //max-width: 1920px;
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3));
  @media screen and (min-width: 993px){
    font-size: calc(100vw * .01);
  }
}

.object-card,
.service-card{
  display: block;
  background-color: $dark;
  position: relative;
  color: #fff;
  &:hover{
    cursor: pointer;
    background-color: transparent;
    .object-card__img,
    .service-card__img{
      &:before{
        background-color: $primary;
        opacity: 1;
        background-image: url(../img/service5.webp);
      }
    }
  }
  &__img{
    @include imgCover(78.125%);
    img{
      mix-blend-mode: luminosity;
    }
    &:before{
      background-color: $black;
      opacity: .35;
      z-index: 3;
      background-position: center;
      background-size: cover;
    }
  }
}

.service-card{
  @media screen and (min-width: 993px){
    &._type2{
      .service-card__info{
        padding: 3.12em;
      }
      .service-card__nav{
        height: 8em;
      }
    }
    &:not(:hover){
      .service-card__nav{
        height: 0;
        margin-top: 0;
        opacity: 0;
      }
    }
  }
  &__info{
    position: absolute;
    z-index: 4;
    padding: 3.12em 4.38em;
    bottom: 0;
    left: 0;
    width: 100%;
  }
  &__title{
    font-size: 2.375em;
    line-height: 131.5%;
    font-weight: 700;
  }
  &__nav{
    display: flex;
    margin-top: 3.25em;
    transition: all .3s;
    height: 3.8125em;
    overflow: hidden;
    opacity: 1;
  }
  &__btn{
    height: 3.8125em;
    span{
      font-size: 1.125rem;
    }
  }
  &__items{
    @include grid100gap(1em);
  }

}

.service-info{
  @include center;
  &__nav{
    margin-top: 1em;
    display: flex;
  }
}

.reviews{
  padding: 6.25em 0;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .3125em));
    gap: .625em;
    margin-top: 4.25em;
  }
}

.review-card{
  padding: 2.35em .88em 1.94em 2.88em;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  background-color: $grayBg;
  &__img{
    width: 13.8125em;
    flex-shrink: 0;
    margin-left: 1em;
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
  &__info{
    @include grid100gap(1.5em);
    max-width: 18.5em;
  }
  .text24{
    letter-spacing: 0.03rem;
  }
}

.reviews-page{
  padding: 12.5em 0;
}

.works{
  overflow: hidden;
  padding: 13.31em 0 16em;
  background-image: url(../img/works-bg.svg);
  background-size: cover;
  background-position: center;
  color: #fff;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 51em) 48.5em;
    gap: 2.5em;
  }
  &__left{
    max-width: 23em;
    padding-top: 2.4em;
  }
  &__tabs{
    margin-top: 4em;
    @include grid100gap(1.75em);
  }
  &__tab{
    opacity: 0.5;
    transition: all .3s;
    cursor: pointer;
    position: relative;
    &:hover,
    &._active{
      opacity: 1;
    }
    &._active{
      pointer-events: none;
      &:before{
        opacity: 1;
      }
    }
    &:before{
      content: '';
      position: absolute;
      top: 50%;
      right: 100%;
      width: 2.0625rem;
      height: .125em;
      background-color: #fff;
      margin-right: 1.25em;
      opacity: 0;
    }
  }
  &__nav{
    display: flex;
    margin-top: 5em;
  }
  &__btn{
    height: 3.8125em;
  }
}

.work-slider{
  height: 50em;
  .swiper{
    &-container{
      height: 100%;
    }
    &-slide{
      height: 50em;
      padding-bottom: 8em;
      &:not(.swiper-slide-active){
        .work-card{
          &__img{
            background-color: $dark;
            img{
              mix-blend-mode: luminosity;
            }
          }
        }
      }
    }
  }
}

.work-card{
  &__img{
    @include imgCover(61.72%);
  }
  &__content{
    display: grid;
    grid-template-columns: 18.5em 22.3125em;
    justify-content: space-between;
    margin-top: 3.8em;
  }
  &__desc{
    @include truncate(3);
  }
  &__nav{
    margin-top: 1.25em;
    display: flex;
  }
  &__link{
    text-decoration: underline;
    font-weight: 700;
    span{
      letter-spacing: 0.0175rem;
    }
  }
}

.certificates{
  padding: 12.5em 0 6.25em;
  &__grid{
    margin-top: 4em;
    display: flex;
  }
}

.certificate-card{
  width: 13.375em;
  padding: 2em .8em;
  min-height: 19.3125em;
  position: relative;
  transition: all .3s;
  &._green{
    background-color: rgba($primary,.1);
  }
  @media screen and (min-width: 993px){
    &._active{
      width: calc(100% - 53.5em);
      background-color: $dark;
      color: #fff;
      .certificate-card{
        padding-right: 13em;
        &__bottom{
          max-width: 15.5em;
          &:before{
            background-color: #fff;
          }
        }
        &__img{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
  }
  &__content{
    display: flex;
    flex-direction: column;
    height: 100%;
  }
  &__bottom{
    margin-top: auto;
    max-width: 10.3125em;
    min-height: 5.3125em;
    padding-top: 1.5em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: .75em;
      left: 0;
      width: 3.125rem;
      height: 0.125rem;
      transform: translateY(50%);
      background-color: rgba($black,.85);
    }
  }
  &__img{
    transition: .3s;
    opacity: 0;
    pointer-events: none;
    position: absolute;
    top: 50%;
    right: 1.5em;
    height: calc(100% - 3em);
    width: 11.375em;
    transform: translateY(-50%);
    img{
      max-height: 100%;
    }
  }
}

.consult{
  color: #fff;
  background-size: cover;
  background-position: center;
  &__inner{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 60em;
    padding: 8.75em 0;
    max-width: 63.125em;
    width: 100%;
    margin: 0 auto;
    text-align: center;
  }
  &__desc{
    margin: 1.25em auto 0;
    max-width: 37.125em;
    .text20{
      line-height: 150%;
    }
  }
}

.consult-form{
  margin-top: 4em;
  max-width: 40.5em;
  width: 100%;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .94em));
    gap: 1.88em;
  }
  &__btn{
    margin: 3.12em auto 0;
    height: 4.3125em;
    width: 16em;
    max-width: 100%;
    img{
      margin-left: .5em;
      width: 1.75em;
      display: block;
      flex-shrink: 0;
    }
    span{
      font-size: 1.125em;
    }
  }
  ._full{
    grid-column: 1/-1;
  }
}

.about{
  padding: 8em 0;
  background-color: $grayBg;
  &__inner{
    max-width: 80.5em;
    width: 100%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 37.5625em calc(100% - 42.1665em);
    justify-content: space-between;
    gap: 2em;
  }
  &__img{
    position: relative;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    position: absolute;
    padding: 1.5em;
    background-color: $dark;
    min-height: 14.1875em;
    width: 18.1875em;
    display: flex;
    align-items: center;
    color: #fff;
    left: 0;
    bottom: 0;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .9em));
    gap: 1.8em;
    margin-top: 2.6em;
  }
  &__item{
    position: relative;
    display: flex;
    align-items: center;
    &:not(:last-child){
      padding-right: .5em;
      &:after{
        content: '';
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        width: 0.0625rem;
        height: 1.875rem;
        background-color: rgba($black,.1);
      }
    }
    &-num{
      white-space: nowrap;
      flex-shrink: 0;
      margin-right: 1.3em;
    }
    &-text{
      line-height: 200%;
    }
  }
  &__nav{
    display: flex;
    margin-top: 3.25em;
  }
}

.about-text{
  @include grid100gap(2.8em);
  &:not(:first-child){
    margin-top: 2.75em;
  }
  p{
    font-size: 1.25em;
    line-height: 163%;
  }
  &__items{
    margin-top: 4em;
    @include grid100gap(1.25em);
  }
  &__item{
    display: flex;
    align-items: center;
    opacity: .6;
    &:after{
      content: '';
      flex-shrink: 0;
      width: 1.61525rem;
      height: 0.125rem;
      margin-left: 1.08em;
      background-color: $black;
    }
  }
}



@media screen and (max-width: 992px){
  .main-preview{
    display: none;
  }

  .main-slide{
    padding: 3em 0 2em;
    &:before{
      opacity: .6;
    }
    &__content{
      max-width: 25em;
      gap: 1em;
    }
  }

  .contacts{
    min-height: unset;
  }

  .map{
    position: static;
    height: 20em;
    min-height: unset;
  }

  .contact-info{
    padding: 1.25em .625em;
    width: 100%;
    &__items{
      gap: 1.5em;
      margin-top: 1em;
    }
    &__nav{
      margin-top: 1em;
    }
  }

  .contact-item{
    gap: .5em;
    &__link{
      @include lastM(0 0 .5em);
    }
  }

  .clients{
    padding: 2.5em 0;
    overflow: hidden;
    &__top{
      display: block;
      &-right{
        margin-top: 1em;
      }
    }
  }

  .clients-slider{
    margin-top: 1.5em;
    .swiper{
      &-container{
        overflow: unset;
      }
      &-slide{
        padding-right: 1em;
      }
    }
    &__img{
      height: 5.5em;
    }
  }

  .advantages{
    padding: 1.25em 0 2.5em;
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - .5em));
      margin-top: 1.5em;
    }
  }

  .services{
    grid-template-columns: 50% 50%;
  }

  .service-card{
    &__img{
      padding-bottom: 70%;
    }
    &__info{
      padding: 1em;
    }
    &__title{
      font-size: 1.5em;
    }
    &__nav{
      margin-top: 1em;
      height: unset;
    }
    &__items{
      gap: .5em;
      .text18{
        line-height: 120%;
      }
    }
  }

  .reviews{
    padding: 1.25em 0;
    &__grid{
      margin-top: 1em;
    }
  }

  .certificate-card,
  .review-card{
    padding: 1em;
    flex-direction: column;
    align-items: flex-start;
    &__img{
      order: -1;
      width: 8em;
      margin: 0 0 .5em;
    }
    &__info{
      gap: .75em;
    }
  }

  .reviews-page{
    padding: 2.5em 0;
  }

  .works{
    padding: 2.5em 0;
    margin-top: 1.25em;
    &__inner{
      grid-template-columns: 100%;
    }
    &__left{
      display: none;
    }
  }

  .work-slider{
    height: unset;
    .swiper{
      &-slide{
        height: unset;
        width: 95%;
        padding: 0 1.5em 0 0;
      }
    }
  }

  .work-card{
    &__content{
      grid-template-columns: 100%;
      gap: 1em;
      margin-top: 1.5em;
    }
  }

  .certificates{
    padding: 2.5em 0 1.25em;
    &__grid{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .375em));
      gap: .75em;
      margin-top: 1em;
    }
  }

  .certificate-card{
    width: 100%;
    min-height: unset;
    display: flex;
    background-color: $grayBg;
    &__img{
      position: static;
      transform: unset;
      opacity: 1;
      pointer-events: unset;
      display: block;
      height: unset;
      img{
        width: 100%;
      }
    }
    &__bottom{
      max-width: 100%;
      min-height: unset;
    }
  }

  .consult{
    &__inner{
      padding: 2.5em 0;
      min-height: unset;
    }
    &__desc{
      margin-top: 1em;
    }
  }

  .consult-form{
    margin-top: 2em;
    &__btn{
      margin-top: 2em;
    }
  }

  .about{
    padding: 2.5em 0;
    &__inner{
      grid-template-columns: 100%;
    }
    &__nav{
      margin-top: 1.5em;
    }
  }

  .about-text{
    gap: 1em;
    p{
      font-size: 1em;
    }
    &__items{
      margin-top: 2em;
    }
  }
}


@media screen and (max-width: 576px){
  .main-slide,
  .contact-info{
    &__nav{
      display: block;
    }
    &__btn{
      width: 100%;
      @include lastM(0 0 .5em);
    }
  }

  .main-slide{
    .text48{
      font-size: 1.125em;
    }
    .text24{
      font-size: .875em;
    }
  }

  .advantages{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .375em));
    }
  }

  .advantage-card{
    padding: 1em .5em;
    &__icon{
      height: 3.5em;
      margin-bottom: 1em;
    }
    &__info{
      gap: .75em;
    }
    &__title{
      padding-bottom: .5em;
    }
  }

  .services{
    grid-template-columns: 100%;
    gap: .5em;
    padding-bottom: 3.5em;
    position: relative;
  }

  .service-info{
    padding: 0 0 .5em;
    .text40{
      text-align: center;
    }
    &__nav{
      position: absolute;
      justify-content: center;
      bottom: .5em;
      left: 0;
      width: 100%;
    }
  }

  .reviews,
  .certificates{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .consult{
    .text48{
      font-size: 1.5em;
    }
  }

  .consult-form{
    &__grid{
      grid-template-columns: 100%;
      gap: 1em;
    }
    &__btn{
      height: 4em;
      width: 100%;
      margin-top: 1em;
    }
  }

  .about{
    &__title{
      position: static;
      background-color: transparent;
      color: $black;
      padding: 0 0 1em;
      min-height: unset;
    }
    &__items{
      grid-template-columns: 100%;
      gap: 1em;
    }
    &__item{
      &:not(:last-child){
        padding: 0 0 1em;
        &:after{
          width: 4em;
          height: .0625em;
          right: unset;
          top: unset;
          bottom: 0;
          left: 0;
        }
      }
      br{
        display: none;
      }
    }
  }
}
