.calc{
  padding: 12.5em 0;
}

.calc-form{
  margin-top: 4em;
  width: 40.375rem;
  max-width: 100%;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .9em));
    gap: 1.8em;
  }
  .fg{
    textarea,
    input{
      background-color: rgba($dark,.05);
      color: $black;
      &::placeholder{
        color: rgba($black,.5);
      }
    }
    textarea{
      height: 12.25rem;
    }
  }
  .file-input{
    background-color: rgba($dark,.05);
    color: rgba($black,.5);
  }
  ._full{
    grid-column: 1/-1;
  }
  .m-check{
    label:before{
      background: #F4F5F6;
      border-color: rgba(255, 255, 255, 0.10);
    }
  }
  &__nav{
    display: flex;
    margin-top: 4em;
  }
}


@media screen and (max-width: 992px){
  .calc{
    padding: 2.5em 0;
  }

  .calc-form{
    width: 100%;
    margin-top: 1.5em;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
    &__nav{
      margin-top: 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .calc-form{
    &__grid{
      grid-template-columns: 100%;
      gap: .5em;
    }
    &__nav{
      margin-top: 1em;
    }
    &__btn{
      width: 100%;
    }
  }
}
