@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.65em;
    height: 3.625em;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 0.875em;
        line-height: 130%;
    }
}

.m-btn-primary{
    background-color: $primary;
    color: #fff;
    &:hover{
        background-color: darken($primary,10%);
        color: #fff;
    }
}

.m-btn-red{
    background-color: $red;
    color: #fff;
    &:hover{
        background-color: darken($red,10%);
        color: #fff;
    }
}


.m-btn-black{
    background-color: $dark;
    color: #fff;
    &:hover{
        background-color: $black;
        color: #fff;
    }
}

.m-btn-white{
    background-color: #fff;
    color: $primary;
    &:hover{
        background-color: $dark;
        color: #fff;
    }
}

.m-btn-white-outline{
    border: 1px solid #fff;
    color: #fff;
    &:hover{
        color: #fff;
        background-color: $dark;
        border-color: $dark;
    }
}

.arrow-link{
    display: flex;
    align-items: center;
    color: $dark;
    &:hover{
        img{
            transform: translateX(.5em);
        }
    }
    span{
        font-size: 1.375em;
    }
    img{
        width: 1.625rem;
        display: block;
        margin-left: .56em;
        flex-shrink: 0;
        transition: all .3s;
    }
}


@media screen and (max-width: 576px){
    .m-btn{
        height: 3em;
    }
}
