.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;

    &__bg {
        background: rgba(#181818, 0.94);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }

    &--active {
        opacity: 1;
        pointer-events: unset;

        .modal__bg {
            display: block;
        }
    }

    &__dialog {
        background: $dark;
        width: 60.625em;
        max-width: calc(100% - 1em);
        position: relative;
        z-index: 3;
        color: #fff;
    }

    &__scroll {
        overflow-y: auto;
        max-height: calc(100 * var(--vh));
        padding: 9em;
    }

    &__close {
        position: absolute;
        top: 2em;
        right: 2em;
        width: 1.75em;

        img,
        svg {
            display: block;
            width: 100%;
            height: auto;
        }
    }
    &__info{
        text-align: center;
        @include grid100gap(1em);
    }
    .consult-form__btn{
        font-weight: 700;
    }
}

.modal-order{
    .modal__dialog{
        background-image: url(../img/modal-bg.svg);
        background-size: cover;
        background-position: center;
    }
}

.modal-thank{
    .modal__dialog{
        background: $primary;
    }
}

.modal-form{
    margin: 4em auto 0;
    width: 34.375rem;
    max-width: 100%;
    @include grid100gap(1.88em);
    .fg{
        input{
            &::placeholder{
                color: #fff;
            }
        }
    }
    .m-btn-white{
        color: $dark;
        font-weight: 700;
        &:hover{
            color: #fff;
            background-color: $black;
        }
    }
}


@media screen and (max-width: 992px){
    .modal{
        &__scroll{
            padding: 2em 1em;
        }
        &__close{
            width: 1em;
            top: 1em;
            right: 1em;
        }
    }

    .modal-form{
        margin-top: 1.5em;
        gap: .5em;
    }
}
