.footer{
  background-color: $dark;
  color: #fff;
  padding: 5.6em 0 3.6em;
  &__bottom,
  &__inner{
    display: flex;
    justify-content: space-between;
    width: 71.25em;
    max-width: 100%;
    margin: 0 auto;
  }
  &__logo{
    display: block;
    width: 11em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    margin-bottom: 2em;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    font-size: 1em;
    line-height: 132%;
    opacity: .7;
    @include lastM(0 0 1.25em);
    &:hover{
      opacity: 1;
    }
  }
  &__bottom{
    padding-top: 1.3em;
    margin-top: 4.25em;
    border-top: 1px solid rgba(255, 255, 255, 0.21);
  }
  &__links2{
    display: flex;
    align-items: center;
  }
  &__link2{
    opacity: .9;
    @include lastM(0 1.25em 0 0);
    &:hover{
      opacity: 1;
    }
  }
  &__by{
    width: 5.4375em;
    display: block;
    img{
      width: 100%;
    }
  }
}

.footer-contacts{
  margin-top: 2em;
  @include grid100gap(1.6em);
  &__address,
  &__time,
  &__link{
    font-size: 1em;
    line-height: 125%;
    font-weight: 700;
  }
  &__address{
    max-width: 16.6875em;
  }
  .social{
    margin-top: .25em;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 2.5em 0 1.5em;
    &__inner{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - 1em));
      gap: 4em 2em;
    }
    &__link{
      @include lastM(0 0 .5em);
    }
    &__bottom{
      flex-direction: column;
      align-items: flex-start;
      margin-top: 2em;
    }
    &__by{
      margin-top: 2em;
    }
  }

  .footer-contacts{
    gap: 1em;
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__inner{
      grid-template-columns: 100%;
      gap: 2em;
    }
    &__title{
      margin-bottom: 1em;
    }
    &__links2{
      flex-direction: column;
      align-items: flex-start;
    }
    &__link2{
      @include lastM(0 0 1em);
    }
  }
}
