.page-top{
    background-color: rgba($dark,.98);
    @media screen and (min-width: 993px){
        .container{
            max-width: 1920px;
        }
    }
    &__inner{
        min-height: 8.1875em;
        padding: 1em 0;
        display: flex;
        align-items: center;
    }
}

.breadcrumbs {
    width: 100%;
    &__item {
        font-size: 1em;
        line-height: 128%;
        position: relative;
        display: inline;
        font-weight: 700;
        color: #fff;
        &:last-child {
            color: $primary;
        }
        &:not(:first-child) {
            margin-left: 1em;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                width: 0.0625rem;
                height: 0.375rem;
                background-color: #fff;
                top: .42em;
                right: 100%;
                margin-right: .5em;
            }
        }
    }
    @media screen and (min-width: 993px){
        &._type2{
            padding-bottom: 1.5625em;
        }
    }
}


@media screen and (max-width: 992px){
    .page-top{
        &__inner{
            min-height: 3.5em;
        }
    }

    .breadcrumbs{
        &__item{
            font-size: .875em;
        }
    }
}
