.file-input,
.fg input,
.fg textarea{
  border: 1px solid #fff;
  padding: 0 1.25em;
  height: 4.375em;
  font-size: 1em;
  color: #fff;
  font-weight: 700;
}

.fg {
  position: relative;
  margin: 0;
  textarea,
  input {
    width: 100%;
    background-color: transparent;
    &::placeholder {
      color: rgba(#fff,.7);
    }
  }
  textarea{
    padding-top: 1.75em;
    padding-bottom: 1.75em;
    resize: none;
    height: 8.75em;
    background: rgba(251, 249, 249, 0.15);
  }
}

.file-input{
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  input{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    opacity: 0;
    cursor: pointer;
  }
  img{
    width: 1.5em;
    flex-shrink: 0;
    margin-left: .5em;
  }
  &__hint{
    display: flex;
    align-items: center;
  }
}

.m-check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        background-color: $primary;
        border-color: $primary;
      }
    }
    &:disabled+label {
      color: #AAAAAA;
      pointer-events: none;
    }
  }
  label {
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
    text-align: left;
    &:before {
      content: '';
      z-index: 1;
      border-radius: 2px;
      background: rgba(251, 249, 249, 0.15);
      border: 1px solid rgba(255, 255, 255, 0.10);
      width: 1.5625rem;
      height: 1.5625rem;
      flex-shrink: 0;
      margin-right: .95em;
      transition: all .100s;
    }
    span{
      font-size: 0.75em;
      line-height: 130%;
    }
  }
}


@media screen and (max-width: 576px){
  .fg{
    input{
      height: 4em;
    }
  }

  .m-check{
    label{
      align-items: unset;
    }
  }

  .file-input{
    &__hint{
      span{
        display: none;
      }
    }
  }
}
