.vacancies{
  padding: 12.5em 0;
  &__desc{
    margin-top: 2em;
    .text14{
      line-height: 207%;
    }
  }
  &__items{
    margin-top: 4.8em;
  }
}

.vacancies-advantages{
  @media screen and (min-width: 993px){
    margin-top: 4em;
    display: grid;
    grid-template-columns: repeat(6,calc(100% / 6 - 2.5em / 6));
    gap: .5em;
    .advantage-card2{
      padding: 2.25em 1.75em;
      .advantage-card__icon{
        margin-bottom: 2em;
      }
      .advantage-card__info{
        max-width: 9.25em;
      }
    }
  }
}

.vacancy-card{
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid rgba($black,.2);
  padding: 1.3em 0;
  &__info{
    display: flex;
    align-items: center;
  }
  &__title{
    opacity: .65;
    display: flex;
    align-items: center;
    &:after{
      content: '';
      flex-shrink: 0;
      display: block;
      background: #595959;
      width: 0.125rem;
      height: 0.625rem;
      margin: 0 .94em 0 .62em;
    }
  }
  &__nav{
    display: flex;
    flex-shrink: 0;
  }
  &__btn{
    margin-left: 1.25em;
  }
}

.vacancies-text{
  margin-top: 4em;
  @include grid100gap(4em);
  &__group{
    @include grid100gap(2.5em);
  }
  ul{
    @include grid100gap(1.5em);
    li{
      display: flex;
      &:before{
        content: '';
        flex-shrink: 0;
        display: block;
        width: 0.9375rem;
        height: 0.125rem;
        margin-right: .94em;
        background-color: $black;
        transform: translateY(.6875em);
      }
      span{
        font-size: 1.125em;
        line-height: 133%;
        letter-spacing: 0.0225rem;
      }
    }
  }
  .vacancy-card__btn{
    &:first-child{
      margin-left: 0;
    }
  }
}


@media screen and (max-width: 992px){
  .vacancies{
    padding: 2.5em 0;
    &__desc{
      margin-top: 1em;
    }
    &__items{
      margin-top: 1.5em;
    }
  }

  .vacancy-card{
    @include grid100gap(.5em);
    &__btn{
      margin: 0 1.25em 0 0;
    }
  }

  .vacancies-text{
    margin-top: 1.5em;
    gap: 1.5em;
    &__group{
      gap: 1em;
    }
    ul{
      font-size: .75em;
      gap: 1em;
      li{
      }
    }
  }
}


@media screen and (max-width: 576px){
  .vacancy-card{
    margin-top: .5em;
    padding: .5em 0;
    gap: .25em;
    &__info{
      @include grid100gap(.25em);
    }
    &__title{
      &:after{
        display: none;
      }
    }
    &__nav{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .25em));
      gap: .5em;
    }
    &__btn{
      margin: 0;
      padding: 0;
      width: 100%;
    }
  }
}
