.blog{
  padding: 12.5em 0;
  .tab-content{
    margin-top: 5em;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 4.5em / 4));
    gap: 3em 1.5em;
  }
}

.blog-card{
  display: flex;
  flex-direction: column;
  &__img{
    display: block;
    @include imgCover(76.1%);
  }
  &__info{
    margin: 1em 0;
    @include grid100gap(.5em);
    .text14{
      line-height: 171%;
      color: rgba($black,.7);
      @include truncate(3);
    }
  }
  &__title{
    font-size: 1em;
    line-height: 187.5%;
    letter-spacing: 0.02rem;
    font-weight: 700;
    @include truncate(2);
  }
  &__bottom{
    margin-top: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: .75em;
    border-top: 1px solid rgba(0, 0, 0, 0.15);
  }
  &__info2{
    display: flex;
    align-items: center;
  }
  &__item{
    color: rgba($black,.7);
    margin-right: 1.25em;
  }
  &__link{
    &:hover{
      color: $dark;
    }
  }
}

.blog-item{
  @media screen and (min-width: 993px){
    .service__inner{
      min-height: 34em;
      padding: 7.5em 0;
    }
  }
}

.article{
  padding: 7.5em 0 6.25em;
  h2,
  h3{
    font-weight: 700;
    margin-bottom: .75em;
  }
  p{
    @include lastM(0 0 1.5em);
  }
  hr{
    border: none;
    display: block;
    height: 2em;
  }
  &__images{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 4em / 3));
    gap: 2em;
    margin: 4em 0 2.5em;
  }
}

.article-img{
  display: block;
  &__wrap{
    display: block;
    @include imgCover(65%);
  }
}

.share{
  margin-top: 3em;
  min-height: 3.95em;
  padding: .5em;
  @include center;
  background-color: $primary;
  color: #fff;
  &__title{
    font-size: 1em;
    line-height: 120%;
    font-weight: 700;
    margin-right: 1em;
  }
}

.blog-collection{
  padding: 6.25em 0 12.5em;
  .blog__grid{
    margin-top: 3em;
  }
}


@media screen and (max-width: 992px){
  .blog{
    padding: 2.5em 0;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
    .tab-content{
      margin-top: 1.5em;
    }
  }

  .article{
    padding: 2.5em 0 1.25em;
    &__images{
      grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
      gap: 1em;
      margin: 2em 0 1.25em;
    }
  }

  .blog-collection{
    padding: 1.25em 0 2.5em;
    .blog__grid{
      margin-top: 1.5em;
    }
  }
}



@media screen and (max-width: 576px){
  .blog{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .article{
    &__images{
      grid-template-columns: 100%;
    }
  }
}
