.text48{
  font-size: 3em;
  line-height: 131.25%;
}

.text40{
  font-size: 2.5em;
  line-height: 150%;
}


.article h2,
.text36{
  font-size: 2.25em;
  line-height: 125%;
  letter-spacing: 0.045rem;
}

.text32{
  font-size: 2em;
  line-height: 140%;
}

.text30{
  font-size: 1.875em;
  line-height: 150%;
}

.article h3,
.text24{
  font-size: 1.5em;
  line-height: 158.3%;
}

.text20{
  font-size: 1.25em;
  line-height: 125%;
}


.article p,
.text18{
  font-size: 1.125em;
  line-height: 161%;
  letter-spacing: 0.0225rem;

}

.text p,
.text14{
  font-size: .875em;
  line-height: 143%;
}

.text12{
  font-size: .75em;
  line-height: 125%;
}

.bold-text{
  font-weight: 700;
}

.medium-text{
  font-weight: 500;
}

.light-text{
  font-weight: 300;
}

.uppercase-text{
  text-transform: uppercase;
}

.underline-text{
  text-decoration: underline;
}

.primary-text{
  color: $primary;
}

.dark-text{
  color: $dark;
}


@media screen and (max-width: 992px){
  .text48{
    font-size: 1.875em;
  }


  .article h2,
  .text40,
  .text36,
  .text30{
    font-size: 1.5em;
  }

  .article h3,
  .text24{
    font-size: 1.125em;
  }

  .text20{
    font-size: 1em;
  }

  .article p,
  .text18{
    font-size: .875em;
  }

  .text14{
    font-size: .75em;
  }
}


@media screen and (max-width: 576px){
  .text48{
    font-size: 1.75em;
  }

  .text30{
    font-size: 1.375em;
  }

  .text24{
    font-size: 1em;
  }
}
