.tabs{
  display: flex;
  flex-wrap: wrap;
  margin-top: 1em;
}

._tab{
  @include center;
  padding: 0 1.69em;
  margin: 1.25em 1.25em 0 0;
  height: 3.625em;
  border: 1px solid $dark;
  transition: all .3s;
  cursor: pointer;
  span{
    font-size: 0.875em;
    line-height: 120%;
  }
  &:hover,
  &._active{
    background-color: $dark;
    color: #fff;
  }
  &._active{
    pointer-events: none;
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.price-page{
  padding: 12.5em 0;
  .tab-content{
    margin-top: 2em;
  }
  &__groups{
    @include grid100gap(6.25em);
  }
}

.price-group{
  .requisite__items{
    margin-top: 0;
    &:not(:first-child){
      margin-top: 2em;
    }
  }
}

.calc-form2{
  width: 100%;
  padding: 6em 6.85em;
  background-image: url(../img/calc-bg.svg);
  background-size: cover;
  background-position: center;
  color: #fff;
  margin-top: 6.25em;
  &__info{
    @include grid100gap(2em);
    max-width: 32em;
  }
  &__grid{
    display: flex;
    margin-top: 3em;
  }
  .fg{
    width: 16.625rem;
    margin-right: 1.5em;
    input{
      height: 3.875em;
    }
  }
  &__btn{
    height: 3.875em;
    padding: 0 1.8em;
    span{
      font-size: 1.125em;
    }
  }
}


@media screen and (max-width: 992px){
  .tabs{
    margin-top: 1em;
    flex-wrap: nowrap;
    width: calc(100% + 1.25em);
    margin-left: -.625em;
    padding: 0 .625em 1em;
    overflow-x: auto;
  }

  ._tab{
    flex-shrink: 0;
    height: 3em;
    padding: 0 1em;
    margin: 0 .5em 0 0;
    span{
      font-size: .75em;
    }
  }

  .price-page{
    padding: 2.5em 0;
    &__groups{
      gap: 1.5em;
    }
  }

  .calc-form2{
    padding: 1.5em;
    margin-top: 2em;
    &__info{
      gap: 1em;
    }
    &__grid{
      margin-top: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .calc-form2{
    padding-left: 1em;
    padding-right: 1em;
    &__grid{
      @include grid100gap(.5em);
    }
    .fg{
      margin: 0;
      width: 100%;
    }
  }
}
